import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IHireDocRequest, IHireDocumentsModel } from "../../models/hire-documents";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IServiceModel } from "app/general-setup/models/service";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class HireDocumentsService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IHireDocRequest) {
		return this.post<IResponse<IHireDocumentsModel>>("HRSetup/HireDocument/Create", body);
	}

	update(body: IHireDocRequest) {
		return this.post<IResponse<IHireDocumentsModel>>("HRSetup/HireDocument/Edit", body);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IHireDocumentsModel[]>>("HRSetup/HireDocument/GetList", {
			params: {
				...params,
				$expand: `${db.Comm_Attachment_Type},${db.Um_Services}`,
			},
		});
	}

	GetAll(params) {
		return this.get<IResponse<IHireDocumentsModel[]>>("HRSetup/HireDocument/GetAll", {
			params: params,
		});
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/HireDocument/Delete", {
			params: { Id: id },
		});
	}

	getDocServiceDropdown(id: number) {
		return this.get<IResponse<IServiceModel[]>>("HRSetup/HireDocument/DropDownServices", {
			params: { id: id },
		}).pipe(
			map((res: IResponse<IServiceModel[]>) => {
				res.data.map((item: IServiceModel) => {
					item.ar_Name = item.arbName;
					item.en_Name = item.engName;
				});
				return res;
			})
		);
	}
}
