import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IServiceModel } from "app/general-setup/models/service";
import {
	IHireDocMap,
	IHireDocRequest,
	IHireDocumentsModel,
} from "app/hr-setup/models/hire-documents";
import { HireDocumentsService } from "app/hr-setup/services/hire-documents/hire-documents.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
	selector: "app-hire-documents",
	templateUrl: "./hire-documents.component.html",
	styleUrls: ["./hire-documents.component.scss"],
})
export class HireDocumentsComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	dataSource: MatTableDataSource<IHireDocumentsModel>;
	mandatoryColomns: string[] = ["Ser", "ar_name", "en_name", "attachments", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	servicesList: IServiceModel[] = [];
	disableServices: boolean = false;
	docName: string;
	constructor(private hireDocumentsService: HireDocumentsService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) {
		//   this.getList(this.currentPage);
		//   this.getDocServiceDropdown();
		// }
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			serviceID: [null, [Validators.required]],
			attachmentIds: [[], [Validators.required]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) {
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
			this.getDocServiceDropdown();
		}
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.hireDocumentsService
			.getList(params)
			.subscribe((res: IResponseList<IHireDocumentsModel[]>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}

					// get servicesIds list ex. [1,2,3]
					const servicesIds = HelpersFunctions.getMatched(res.data, "serviceID");

					// Distinct comm_Attachment_Type for serviceId
					let result = [];
					servicesIds.map((serviceId) => {
						let obj = {} as IHireDocMap;
						obj.id = serviceId;
						obj.attachmentsList = [];
						obj.attachments = [];
						res.data.map((e) => {
							if (e.serviceID == serviceId) {
								obj.ar_name = e.uM_Services.arbName;
								obj.en_name = e.uM_Services.engName;
								obj.attachmentsList.push(e.comm_Attachment_Type);
								obj.attachments.push(
									`  ${this.msgTranslate(
										e.comm_Attachment_Type.ar_Name,
										e.comm_Attachment_Type.en_Name
									)}  `
								);
							}
						});
						obj.attachments.join(" - ");
						result.push(obj);
					});

					this.dataSource = new MatTableDataSource(result);
					this.page.length = res.meta.totalItemCount;
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	onsubmit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IHireDocRequest = {
				ID: this.form.value.id,
				Service_ID: this.form.value.serviceID,
				Attachments_IDs: this.form.value.attachmentIds,
			};
			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(body: IHireDocRequest) {
		this.showLoading();
		this.hireDocumentsService.create(body).subscribe((res: IResponse<IHireDocumentsModel>) => {
			if (res.message?.messageType == MessageTypes.Success) {
				this.getDocServiceDropdown();
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else {
				this.errorList(res.message?.messages ?? [res.message.messageTypeName]);
				this.hideLoading();
			}
		});
	}

	update(body: IHireDocRequest) {
		this.showLoading();
		this.hireDocumentsService.update(body).subscribe((res: IResponse<IHireDocumentsModel>) => {
			if (res.message?.messageType == MessageTypes.Success) {
				this.getDocServiceDropdown();
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize);
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message?.messages);
			this.hideLoading();
		});
	}

	hireDocumentsToEdit(doc: IHireDocMap) {
		this.disableServices = true;
		let attachmentIds: number[] = [];
		doc.attachmentsList.forEach((e) => {
			attachmentIds.push(e.id);
		});

		this.formCtrls.serviceID.setValue(doc.id);
		this.formCtrls.id.setValue(doc.id);
		this.formCtrls.attachmentIds.setValue(attachmentIds);
		this.docName = this.msgTranslate(doc.ar_name, doc.en_name);
	}

	onDelete(doc: IHireDocMap): void {
		let msg = this.msgTranslate(doc.ar_name, doc.en_name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(doc);
		});
	}

	delete(doc: IHireDocMap) {
		this.showLoading();
		this.hireDocumentsService.Delete(doc.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.getDocServiceDropdown();
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (doc.id == this.formCtrls.ID.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	getDocServiceDropdown() {
		// let model: IServiceModel;
		this.showLoading();
		this.hireDocumentsService
			// .getDocServiceDropdown(model.id)
			.getDocServiceDropdown(57)
			.subscribe((res: IResponse<IServiceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.servicesList = res.data;
				} else {
					this.errorList(res.message.messages);
					this.hideLoading();
				}
			});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
		this.disableServices = false;
	}
}
