import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IAttachmenModel } from "app/general-setup/models/attachments";
import {
	IDocJobTitleMap,
	IDocJobTitleModel,
	IDocJobTitleRequest,
	IHireDocumentsModel,
} from "app/hr-setup/models/hire-documents";

import { AssignDocToJobtitleService } from "app/hr-setup/services/hire-documents/assign-doc-to-job.service";
import { HireDocumentsService } from "app/hr-setup/services/hire-documents/hire-documents.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { db } from "app/shared/resources/database-tabels";

@Component({
  selector: "app-assign-attachment-to-job",
  templateUrl: "./assign-attachment-to-job.component.html",
  styleUrls: ["./assign-attachment-to-job.component.scss"],
})
export class AssignAttachmentToJobComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	attachmentList: IAttachmenModel[];
	mandatoryColomns: string[] = ["Ser", "jobTitleName", "attachment", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IDocJobTitleModel>;

  constructor(
    private jobTitleService: AssignDocToJobtitleService,
    private hireDocumentsService: HireDocumentsService
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0],
      serviceId: [null, [Validators.required]],
      Attachments_IDs: [[], [Validators.required]],
      SelecetedJobTitle: [[], Validators.required],
    });

    this.formCtrls.serviceId.valueChanges.subscribe((serviceId) => {
      if (serviceId > 0) {
        this.getAttachmentByServiceId();
      } else {
        this.attachmentList = [];
        this.formCtrls.Attachments_IDs.disable();
      }
    });

    this.formCtrls.Attachments_IDs.disable();
  }

  get formCtrls() {
    return this.form.controls;
  }

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) {
			this.getList(this.firstPage);
		}
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.jobTitleService.getList(params).subscribe((res: IResponseList<IDocJobTitleModel[]>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				res.data.map((e: IDocJobTitleMap | any) => {
					e.jobTitleName = this.msgTranslate(e.core_Job_Title.ar_Name, e.core_Job_Title.en_Name);
					e.attachment = this.msgTranslate(
						e.comm_Attachment_Type_Services.comm_Attachment_Type.ar_Name,
						e.comm_Attachment_Type_Services.comm_Attachment_Type.en_Name
					);
				});

          //#region start join data with jobId
          // let result = [];
          // const jobIds = this.getMatched(res.data, "job_Title_ID");
          // jobIds.map((jobId) => {
          //   let obj = {} as IDocJobTitleMap;
          //   obj.id = jobId;

          //   obj.attachmentsList = [];
          //   obj.attachments = [];
          //   res.data.map((e: IDocJobTitleMap) => {
          //     if (e.job_Title_ID == jobId) {
          //       obj.jobTitleName = this.msgTranslate(
          //         e.core_Job_Title.ar_Name,
          //         e.core_Job_Title.en_Name
          //       );
          //       obj.attachments.push(
          //         `  ${this.msgTranslate(
          //           e.comm_Attachment_Type_Services.comm_Attachment_Type
          //             .ar_Name,
          //           e.comm_Attachment_Type_Services.comm_Attachment_Type.en_Name
          //         )}  `
          //       );
          //     }
          //   });
          //   result.push(obj);
          // });
          //#endregion

				this.dataSource = new MatTableDataSource(res.data);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else this.errorList(res.message.messages);
		});
	}

  create() {
    let model: IDocJobTitleRequest = {
      Attachments_IDs: this.form.value.Attachments_IDs,
      SelecetedJobTitle: this.form.value.SelecetedJobTitle,
    };
    console.log('model',model)
    this.submitted = true;
    this.showLoading();
    this.jobTitleService
      .create(model)
      .subscribe((res: IResponseList<IDocJobTitleModel[]>) => {
        if (this.form.valid) {
          if (res.message.messageType == MessageTypes.Success) {
            this.getAttachmentByServiceId();
            this.clearForm();
            this.getList(this.lastPage, this.page.pageSize);
            this.addSuccessfullyMsg(res?.message?.messageTypeName);
          } else {
            this.errorList(
              res.message?.messages ?? [res.message.messageTypeName]
            );
            this.hideLoading();
          }
        }
      });
  }

  onDelete(jobTitle: IDocJobTitleMap): void {
    this.deleateDialog(jobTitle.jobTitleName).subscribe((confirm) => {
      if (confirm) this.delete(jobTitle);
    });
  }

  delete(jobTitle: IDocJobTitleMap) {
    let params = {
      Job_Title_ID: jobTitle.job_Title_ID,
      AttachmentTypeService_ID: jobTitle.attachmentTypeService_ID,
    };
    this.showLoading();
    this.jobTitleService.Delete(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        } else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (jobTitle.job_Title_ID == this.formCtrls.ID.value) this.clearForm();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.formCtrls.serviceId.enable();
    this.submitted = false;
  }

  getAttachmentByServiceId() {
    let params = {
      $filter: `serviceid eq ${this.formCtrls.serviceId.value}`,
      $expand: `${db.Comm_Attachment_Type},${db.Um_Services}`,
    };
    this.showLoading();
    this.hireDocumentsService
      .GetAll(params)
      .subscribe((res: IResponse<IHireDocumentsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.formCtrls.Attachments_IDs.enable();
          const list: IAttachmenModel[] = [];
          res.data.map((e) => {
            list.push(e.comm_Attachment_Type);
          });
          this.attachmentList = list;
        } else {
          this.errorList(res.message?.messages);
          this.hideLoading();
        }
      });
  }
}
