import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  IGovernorateModel,
  IGovernorateRequest,
} from "app/hr-setup/models/governorate";
import { GovernorateService } from "app/hr-setup/services/governorate.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-governorate",
  templateUrl: "./governorate.component.html",
  styleUrls: ["./governorate.component.scss"],
})
export class GovernorateComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	dataSource: MatTableDataSource<IGovernorateModel>;
	mandatoryColomns: string[] = ["Ser", "id", "ar_Name", "en_Name", "country", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

  constructor(private governorateService: GovernorateService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      countrY_ID: [null, [Validators.required]],
      ar_Name: [null, [Validators.required]],
      en_Name: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.governorateService.getList(params).subscribe((res: IResponseList<IGovernorateModel[]>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				res.data.map((e) => {
					e.country = this.msgTranslate(e.comm_Country.ar_Name, e.comm_Country.en_NAme);
				});
				this.dataSource = new MatTableDataSource(res.data);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else this.errorList(res.message.messages);
		});
	}

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IGovernorateRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
        Country_ID: this.form.value.countrY_ID,
      };
      model.ID == 0 ? this.create(model) : this.update(model);
    }
  }

  create(body: IGovernorateRequest) {
    this.showLoading();
    this.governorateService
      .create(body)
      .subscribe((res: IResponse<IGovernorateModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else {
          this.errorList(res?.message?.messages);
          this.hideLoading();
        }
      });
  }

  update(body: IGovernorateRequest) {
    this.showLoading();
    this.governorateService
      .update(body)
      .subscribe((res: IResponse<IGovernorateModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  governorateToEdit(governorate: IGovernorateModel) {
    this.form.patchValue(governorate);
  }

  onDelete(governorate: IGovernorateModel): void {
    let msg = this.msgTranslate(governorate.ar_Name, governorate.en_Name);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(governorate);
    });
  }

  delete(governorate: IGovernorateModel) {
    this.showLoading();
    this.governorateService.Delete(governorate.id).subscribe((res: any) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        } else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (governorate.id == this.formCtrls.id.value) this.clearForm();
        if (governorate.id == this.formCtrls.id.value) this.clearForm();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
